function swap (arr, i, j) {
  let temp = arr[i];
  arr[i] = arr[j];
  arr[j] = temp;
}

export default function shuffle (arr, start, end) {
  if (typeof(start) === 'undefined')
    start = 0;

  if (typeof(end) === 'undefined')
    end = arr.length;

  let diff = end - start;
  for (let i = start; i < end; i++)
    swap(arr,
      start + Math.floor(Math.random() * diff),
      start + Math.floor(Math.random() * diff));
}