<!--suppress HtmlUnknownTag, HtmlUnknownAttribute -->
<template>
  <div>
    <h3>Out-of-Context Hillwalkers' Quotes</h3>
    <p>
      A pseudo-random collection of the funniest and most accidentally spoken words and adages by our club members are here for your enjoyment:
    </p>

    <Notifications :alerts="alerts" />

    <v-card>
      <v-card-text v-if="quoteVisible" style="min-height:250px">
        <span v-html="this.quotes[this.current]" />
      </v-card-text>

      <v-card-actions>
        <v-btn :disabled="!quotes.length"
               :loading="!!restCallsGoing"
               @click="nextQuote()">
          {{ buttonText }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
  import blockShuffle from '../lib/blockShuffle.js';
  import marked from 'marked';
  import AxiosHelper from '../components/AxiosHelper.vue';
  import Notifications from '../components/Notifications.vue';

  export default {
    name: 'SUHC_Quotes',
    components: {Notifications},
    mixins: [AxiosHelper],

    data: () => ({
      blankOrMore: '',
      current: -1, // Ugh hacks
      quotes: []
    }),

    computed: {
      quoteVisible () {
        return this.current >= 0;
      },

      buttonText () {
        return this.quoteVisible ? 'More' : 'Show me!';
      }
    },

    methods: {
      nextQuote () {
        this.current = (this.current + 1) % this.quotes.length;
        this.blankOrMore = ' more';
      }
    },

    mounted () {
      this.plainAxios('retrieving quotes', axios => axios.get(this.$dataUrl + '/suhc/quotes.aggregated.json'), data => {
        data.reverse();
        blockShuffle(data, 35);
        this.quotes = data.map(one => marked(one, { smartypants: true, gfm: true, breaks: true }));
      });
    }
  }
</script>
